<template>
    <article>
        <h1><code>&lt;veui-textarea&gt;</code></h1>
        <h2></h2>
        <div class="options-desc">
             <span class="bg-gray-show">ui</span> 
             <span class="bg-gray-show">s / m</span>
        </div>
        <section>
            <veui-form>
                <veui-field
                    ui="s"
                    label="s"
                >
                    <veui-textarea
                        v-model="value"
                        autofocus
                        ui="s"
                        rows="3"
                    />
                </veui-field>
                <veui-field
                    ui="s"
                    label="m"
                >
                    <veui-textarea
                        v-model="value"
                        autofocus
                        ui="m"
                        rows="3"
                        resizable
                    />
                </veui-field>
            </veui-form>
        </section>
        <section>
            <h3></h3>
            <veui-textarea
                placeholder=""
                v-model="value"
                readonly
            />
        </section>
        <section>
            <h3></h3>
            <veui-textarea
                placeholder=""
                v-model="value"
                disabled
            />
        </section>
        <section>
            <h3></h3>
            <veui-textarea
                placeholder=""
                v-model="value"
                invalid
            />
        </section>

        <section>
            <h3>()</h3>
            <section>
                <veui-textarea
                    placeholder=""
                    maxlength="5"
                    strict
                />
            </section>
        </section>

        <section>
            <h3></h3>
            <section>
                <veui-textarea
                    resizable
                    placeholder=""
                />
            </section>
        </section>
        <section>
            <h3></h3>
            <veui-textarea
                :value="fixed"
                composition
                line-number
                autofocus
                ui="s"
                rows="3"
                resizable
            />
            <h3></h3>
            <veui-textarea
                :value="fixed"
                line-number
                autofocus
                ui="s"
                rows="3"
                resizable
            />
            <h3>,  v-model value: {{ controlled1 }}</h3>
            <veui-textarea
                v-model="controlled1"
                composition
                line-number
                autofocus
                ui="s"
                rows="3"
                resizable
            />
            <h3>,  v-model value: {{ controlled2 }}</h3>
            <veui-textarea
                v-model="controlled2"
                line-number
                autofocus
                ui="s"
                rows="3"
                resizable
            />
            <h3>localValue{{ uncontrolled1 }}</h3>
            <veui-textarea
                ref="text2"
                composition
                line-number
                autofocus
                ui="s"
                rows="3"
                resizable
                @input="uncontrolled1 = $event"
            />
            <h3>localValue{{ uncontrolled2 }}</h3>
            <veui-textarea
                ref="text2"
                line-number
                autofocus
                ui="s"
                rows="3"
                resizable
                @input="uncontrolled2 = $event"
            />
        </section>
    </article>
</template>

<script>
import bus from '../bus';
import {Textarea, Form, Field} from 'veui';

export default {
    name: 'button-demo',
    components: {
        'veui-textarea': Textarea,
        'veui-field': Field,
        'veui-form': Form
    },
    data() {
        return {
            value: '',
            fixed: '',
            controlled1: '',
            controlled2: '',
            uncontrolled1: '',
            uncontrolled2: ''
        };
    },
    mounted() {
        this.$children.forEach(child => {
            child.$on('click', () => {
                bus.$emit('log', child.$el.getAttribute('ui'));
            });
        });
    }
};
</script>

