<script>
import {VeuiTabs, VeuiTab, VeuiButton, VeuiForm, VeuiField} from 'veui';
import {findIndex, uniqueId} from 'lodash';

export default {
    name: 'tabs-demo',
    components: {
        VeuiTabs,
        VeuiTab,
        VeuiButton,
        VeuiForm,
        VeuiField
    },
    data() {
        return {
            label1: '',
            label2: '',
            label3: '',
            totalTabs0: 15,
            totalTabs1: 20,
            tabs0: [
                {label: '', name: '1'},
                {label: '2', name: '2'},
                {label: '3', name: '3'}
            ],
            tabs1: [
                {label: '1', name: '1'},
                {label: '', name: '2', status: 'info'},
                {label: '3', name: '3'}
            ],
            tabs2: [
                {label: '1', name: '1'},
                {label: '', name: '2', status: 'warning'},
                {label: '3', name: '3'}
            ],
            tabs3: [
                {label: '1', name: '1', status: 'error'},
                {label: '', name: '2', status: 'warning'},
                {label: '3', name: '3'}
            ],
            active0: '',
            active1: '',
            active2: '',
            active3: '',
            active4: '',
            active5: '',
            insertVisiable: false,
            tabIfRemoving: false
        };
    },
    methods: {
        addTab0() {
            if (this.tabs0.length >= this.totalTabs0) {
                return;
            }

            let label = uniqueId('');
            this.tabs0.push({
                label,
                name: label
            });
            this.active1 = label;
        },
        addTab1() {
            if (this.tabs1.length >= this.totalTabs1) {
                return;
            }

            let label = uniqueId('');
            this.tabs1.push({
                label,
                name: label
            });
        },
        addTab2() {
            let label = uniqueId('');
            this.tabs2.push({
                label,
                name: label
            });
        },
        addTab3() {
            let label = uniqueId('');
            this.tabs3.push({
                label,
                name: label
            });
        },
        removeTab0({name}) {
            let index = findIndex(this.tabs0, tab => tab.name === name);
            if (index !== -1) {
                this.tabs0.splice(index, 1);
            }
        },
        removeTab1({name}) {
            let index = findIndex(this.tabs1, tab => tab.name === name);
            if (index !== -1) {
                this.tabs1.splice(index, 1);
            }
        },
        removeTab2({name}) {
            let index = findIndex(this.tabs2, tab => tab.name === name);
            if (index !== -1) {
                this.tabs2.splice(index, 1);
            }
        },
        removeTab3({name}) {
            let index = findIndex(this.tabs3, tab => tab.name === name);
            if (index !== -1) {
                this.tabs3.splice(index, 1);
            }
        }
    }
};
</script>

