<script>
import bus from '../bus';
import {VeuiInput, VeuiField, VeuiForm, VeuiIcon, VeuiButton} from 'veui';
import nudge from 'veui/directives/nudge';
import 'veui-theme-blue-icons/user-manager';

export default {
    name: 'text-input',
    components: {
        VeuiInput,
        VeuiField,
        VeuiForm,
        VeuiIcon,
        VeuiButton
    },
    directives: {
        nudge
    },
    data() {
        return {
            value: '',
            key: null,
            name: null,
            phone: '13800138000',
            password: null,
            hiddenValue: '',
            poem: '',
            placeholder: '1~30',
            price: '1024',
            options: [
                {
                    label: 'https://',
                    value: 'https://'
                },
                {
                    label: 'http://',
                    value: 'http://'
                }
            ],
            controlled1: '',
            controlled2: '',
            uncontrolled1: '',
            uncontrolled2: ''
        };
    },
    methods: {
        log(item) {
            bus.$emit('log', item);
        },
        delaySet() {
            setTimeout(() => {
                this.controlled1 = this.controlled2 = '123';
            }, 3000);
        },
        handleThumbNudgeUpdate(delta) {
            let val = this.price;

            let digits;
            let unit;
            if (typeof val === 'string') {
                let matched = val.match(/^(\d+(?:\.\d+)?)(.*)$/);
                if (!matched) {
                    return;
                }
                [digits, unit] = matched.slice(1).digits;
                digits = parseFloat(digits);
                if (isNaN(digits)) {
                    return;
                }
            } else if (typeof val === 'number') {
                digits = val;
            } else {
                return;
            }

            //  0.1  0.30000000000000004
            let newVal = Math.round((digits + delta) * 10) / 10;
            if (unit !== undefined) {
                newVal += unit;
            }

            this.price = newVal;
        },
        handlePriceChange(val) {
            this.price = val;
        }
    }
};
</script>

