<template>
    <article>
        <h1><code>&lt;veui-pagination&gt;</code></h1>
        <h2></h2>
        <div class="options-desc">
            <span class="bg-gray-show">ui</span>
            
            <span class="bg-gray-show">xs / s / m</span>
        </div>
        <h2></h2>
        <div class="options-desc">
            <span class="bg-gray-show">ui</span>
            
            <span class="bg-gray-show">normal / </span>
        </div>
        <section>
            <h3>[ui=normal]</h3>
            <veui-form>
                <veui-field label="[ui=xs]">
                    <veui-pagination
                        :page="page"
                        :total="total"
                        :page-sizes="pageSizes"
                        :to="to"
                        ui="xs normal"
                        :page-size.sync="pageSize"
                    />
                </veui-field>
                <veui-field label="[ui=s]">
                    <veui-pagination
                        :page="page"
                        :total="total"
                        :to="to"
                        ui="normal"
                    />
                </veui-field>
                <veui-field label="[ui=m]">
                    <veui-pagination
                        :page="page"
                        :total="total"
                        ui="m normal"
                        :to="to"
                    />
                </veui-field>
            </veui-form>
        </section>

        <section>
            <h3></h3>
            <veui-form>
                <veui-field label="[ui=xs]">
                    <veui-pagination
                        :page="page"
                        :total="total"
                        :page-sizes="pageSizes"
                        :to="to"
                        goto
                        ui="xs"
                        :page-size.sync="pageSize"
                    />
                </veui-field>
                <veui-field label="[ui=s]">
                    <veui-pagination
                        :page="page"
                        :total="total"
                        :to="to"
                        goto
                    />
                </veui-field>
                <veui-field label="[ui=m]">
                    <veui-pagination
                        ui="m"
                        :page="page"
                        :total="total"
                        :to="to"
                        goto
                    />
                </veui-field>
            </veui-form>
        </section>

        <section>
            <h3></h3>
            <small>6</small>
            <div class="veui-pagination-simple">
                <veui-button></veui-button>
                <veui-button class="next-page"></veui-button>
            </div>
        </section>

        <section>
            <h3></h3>
            <small> &lt;router-link&gt;  to prop </small>
            <section>
                <veui-pagination
                    :page="page"
                    goto
                    :total="total"
                    :to="{ name: 'Pagination', params: { page: ':page' } }"
                />
            </section>
        </section>

        <section>
            <h3></h3>
            <section>
                <veui-pagination
                    :page="page"
                    :total="total"
                    :to="to"
                    goto
                    native
                />
            </section>
        </section>

        <section>
            <h3></h3>
            <small> <code>to</code> </small>
            <section>
                <veui-pagination
                    :page="page"
                    :total="total"
                    :to="to"
                    native
                    goto
                    @redirect="handlePageRedirect"
                />
            </section>
            <div class="message">
                {{ fifthPaginationMessage }}
            </div>
        </section>

        <section>
            <h3></h3>
            <section>
                <veui-pagination
                    :page="p"
                    :total="total"
                    @redirect="handleCustomRedirect"
                />
            </section>
        </section>

        <section>
            <h3></h3>
            <section>
                <veui-pagination
                    :page="1"
                    :total="0"
                    :to="to"
                    native
                />
            </section>
        </section>
    </article>
</template>

<script>
import bus from '../bus';
import {Pagination, Form, Field, Button} from 'veui';

export default {
    name: 'pagination-demo',
    components: {
        'veui-pagination': Pagination,
        'veui-form': Form,
        'veui-field': Field,
        'veui-button': Button
    },
    data() {
        return {
            page: parseInt(this.$route.params.page, 10) || 1,
            total: 500,
            to: '/pagination/:page',
            pageSize: 30,
            pageSizes: [30, 60, 100, 200],
            fifthPaginationMessage: '',
            p: 1
        };
    },
    mounted() {
        this.$children.forEach(child => {
            child.$on('click', () => {
                bus.$emit('log', child.$el.getAttribute('ui'));
            });
        });
    },
    methods: {
        handlePageRedirect(page, event) {
            event.preventDefault();
            this.fifthPaginationMessage = `${page}`;
        },
        handleCustomRedirect(page) {
            this.p = page;
        }
    },
    beforeRouteUpdate({params}, from, next) {
        let page = parseInt(params.page, 10);
        this.page = isNaN(page) ? 1 : page;
        next();
    }
};
</script>

<style lang="less" scoped>
@import (reference) url('~@/variables/index.less');
section {
    margin-bottom: 32px;
    small {
        display: block;
        margin-bottom: 16px;
    }
    section {
        display: flex;
        margin-bottom: 0;
    }
    .veui-pagination {
        flex: 1;
    }
    .message {
        margin-top: 16px;
        font-size: 12px;
        color: #666;
        text-align: right;
    }
}
</style>
